import React, { ForwardRefRenderFunction, useImperativeHandle, MutableRefObject, forwardRef, useCallback, ReactElement, isValidElement } from 'react';
import { Form, Row, Col, Input, Select, DatePicker } from 'antd';
import moment from 'moment';


type SupportRenderItem = 'input' | 'passwordInput' | 'select' | 'rangePicker' | 'datePicker'

interface ColType {
  span: number;
}

interface FormItemLayout {
  labelCol: ColType;
  wrapperCol: ColType;
}
export interface FormItem {
  name: string;
  label: string;
  renderCom: SupportRenderItem | ReactElement;
  comProps?: {
    options?: SelectOptions[];
    placeholder?: string;
    [key: string]: any;
  };
  span?: number;
  itemProps?: { };
}

export interface SelectOptions {
  label: string | number;
  value: string | number;
}


export interface Props {
  ref: MutableRefObject<any>;
  items: FormItem[];
  onPressEnter?: () => any;
  initialValues?: {[keyName: string]: any};
  formItemLayout: FormItemLayout;
}

function trimString(objToTrim: {[k: string]: any}) {
  const fields = { ...objToTrim };

  Object.keys({ ...fields }).forEach((keyName: string) => {
    if (typeof fields[keyName] === 'string') {
      fields[keyName] = fields[keyName].trim();
    }
  });

  return fields;
}

const FormRender: ForwardRefRenderFunction<unknown, Props> = (props: Props, ref: MutableRefObject<any>) => {
  const [form] = Form.useForm();

  async function getValues() {
    const formValue = await form.validateFields();

    return trimString(formValue);
  }

  useImperativeHandle(ref, () => ({
    resetFrom: form.resetFields,
    getValues,
  })
  );

  const getChildren = useCallback(({ renderCom, comProps = {}, label }: FormItem) => {

    if (isValidElement(renderCom)) {
      return renderCom;
    }
    switch (renderCom) {
      case 'input':
        return <Input placeholder={ comProps?.placeholder ?? `请输入${ label }` } onPressEnter={ () => props.onPressEnter && props.onPressEnter() } />;
      case 'passwordInput':
        return <Input.Password placeholder={ comProps?.placeholder ?? `请输入${ label }` } onPressEnter={ () => props.onPressEnter && props.onPressEnter() } />;
      case 'select':
        return (
          <Select {
            ...{
              allowClear: true,
              placeholder: `请选择${ label }`,
              onChange: () => props.onPressEnter && props.onPressEnter(),
              ...comProps
            } }>
            {
              comProps?.options?.map((item: SelectOptions) => (
                <Select.Option value={ item.value } key={ item.value }>{item.label}</Select.Option>
              ))
            }
          </Select>
        );
      case 'rangePicker':
        return (
          <DatePicker.RangePicker showTime={ true } style={{ width: '100%' }}
            ranges={{
              '今天': [moment().startOf('day'), moment().endOf('day')],
              '昨天': [moment().startOf('day').subtract(1, 'day'), moment().endOf('day').subtract(1, 'day')],
              '本周': [moment().startOf('week'), moment().endOf('week')],
              '本月': [moment().startOf('month'), moment().endOf('month')]
            }}
            { ... comProps }
          />);
      case 'datePicker':
        return (
          <DatePicker style={{ width: '100%' }}
            { ... comProps }
          />);
      default:
        return <Input />;
    }
  }, [props.onPressEnter]);


  return (
    <Form form={ form }
      initialValues={ props.initialValues }
    >
      <Row gutter={ 24 }>
        {
          props.items.map((item: FormItem) => {
            return (
              <Col span={ item.span ?? 8 } key={ item.name }>
                <Form.Item name={ item.name } label={ item.label } { ...props.formItemLayout } { ...item.itemProps }>
                  {
                    getChildren(item)
                  }
                </Form.Item>
              </Col>
            );
          } )
        }
      </Row>
    </Form>
  );
};


export default forwardRef(FormRender);
