import React, { FC, useRef, useState, useMemo, useCallback } from 'react';
import {
  SyncOutlined,
  UserOutlined
} from '@ant-design/icons';
import { Link } from 'umi';
import QueryTable, { Props as QueryTableProps } from '@/components/QueryTable';
import { ColumnsType } from 'antd/lib/table';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import { Button, message, Avatar } from 'antd';
import PreviewImg from '@/components/PreviewImg';
import AddExpertModal from '@/components/AddExpertModal';
import { UserOutline } from '@/components/miniComponents';
import { UserItemInfo } from './types';
import { UserDetailInfo } from '@/types/types';

import { useGetUserList } from '../NormalUserList/services';
import { createExpertUser, updateExpertUser } from './services';
import { useGetUserCommontNum } from '@/services/publicServices';

interface QueryParams {
  current: number;
  pageSize: number;
  [key: string]: any;
}

const ROLE_TYPE = 'expert';
const PAGESIZE = 10;
const initSearch = {
  pagination: { pageSize: PAGESIZE, current: 1 },
  query: { roleType: ROLE_TYPE },
  sort: { type: 'desc', key: 'create_time' }
};

const ExpertList: FC = () => {
  const formRef = useRef<any>();
  const queryTable = useRef<any>();
  const [searchParams, setSearchParams] = useState(initSearch);
  const { userList, pagination, isValidating, mutate } = useGetUserList(searchParams);
  const [addExpertVisible, setAddExpertVisible] = useState(false);
  const [targetExport, setTargetExport] = useState<UserItemInfo>();

  const userIds = useMemo(() => {
    return userList?.map((item: UserDetailInfo) => item?.userId);
  }, [userList]);
  const { UserCommontNum, isValidating: commontNumLoading } = useGetUserCommontNum(userIds);

  // 查询列表数据
  function query(params: QueryParams) {
    // eslint-disable-next-line no-shadow
    const { pagination, query } = params;

    setSearchParams((preParams: any) => ({
      ...preParams,
      pagination,
      query: {
        ...query,
        roleType: ROLE_TYPE
      }
    }));
  }


  const handleShowAddExpertVisible = useCallback((ex: UserItemInfo) => {
    setAddExpertVisible(true);
    setTargetExport(ex);
  }, []);
  const handleHiddenAddExpertVisible = useCallback(() => {
    setAddExpertVisible(false);
  }, []);

  // Todo 专业评论数量字段暂时没有
  const columns: ColumnsType<any> = [
    { title: '头像',
      dataIndex: 'userAvatar',
      width: 120,
      align: 'center',
      render: (text: string) => (
        <PreviewImg imgUrls={ text } placement="right">
          <Avatar size={ 64 } src={ text } icon={ <UserOutlined /> } />
        </PreviewImg>)
    },
    { title: '专家昵称', align: 'center', dataIndex: 'nickName' },
    { title: '专家身份', align: 'center', dataIndex: 'userDescription' },
    { title: '专业评论数量', align: 'center', render: (_: any, record: any) => UserCommontNum[record?.userId] || 0 },
    // { title: '状态',
    //   dataIndex: 'status',
    //   align: 'center',
    //   render: (text: number, record: UserItemInfo ) => (
    //     <>
    //       {
    //         text === 110
    //           ? <span style={{ color: '#ff4d4f' }}>封禁</span>
    //           : <span>正常</span>
    //       }
    //       <br />
    //       <UserOutline user={ record } isExpert={ true } refresh={ mutate } />
    //     </>
    //   )
    // },
    { title: '操作',
      align: 'center',
      render: (_: any, record: UserItemInfo) => (
        <>
          <Link to={ `/user-manage/user/${ record?.userId }?isExport=true` }><Button>管理</Button></Link>
          <br />
          <Button style={{ marginTop: '5px' }} type="primary" onClick={ () => handleShowAddExpertVisible(record) }>修改资料</Button>
        </>
      ) }
  ];
  

  const formItem: QueryTableProps['formItem'] = [
    {
      name: 'nickName',
      label: '名称',
      renderCom: 'input',
    }
  ];

  const handleAddExpert = useCallback(async (params: any) => {
    try {
      let res: any = {};

      if (params?.userId) {
        res = await updateExpertUser(params);
      } else {
        res = await createExpertUser(params);
      }
      if (res?.code === 200) {
        message.success('操作成功！');
        handleHiddenAddExpertVisible();
        mutate();
      }
    } catch (error) {
      console.error(error?.message || '服务器出问题啦');
    }
  }, [handleHiddenAddExpertVisible, mutate]);

  return (
    <React.Fragment>
      <AddExpertModal visible={ addExpertVisible }
        targetExport={ targetExport }
        onCancel={ handleHiddenAddExpertVisible }
        onOk={ handleAddExpert }
      />
      <QueryTable notQueryWhilestart={ true }
        formItem={ formItem }
        columns={ columns }
        rowKey="userId"
        formRef={ formRef }
        ref={ queryTable }
        query={ query }
        list={ userList }
        total={ pagination?.total }
        loading={ isValidating }
        plusAction={
          <div>
            <Button type="primary" onClick={ () => handleShowAddExpertVisible({}) }>添加专家</Button>
            <Button onClick={ mutate } icon={ <SyncOutlined /> } type="primary" shape="circle" style={{ marginLeft: '20px' }} />
          </div> }
      />
    </React.Fragment>
  );
};

export default withAuthorized(ExpertList, [Role.SYSTEM, Role.EXPERT]);
