import React, { useState, Suspense, useCallback } from 'react';
import { Upload, message } from 'antd';
import { PlusOutlined, EyeOutlined, FormOutlined, DeleteOutlined } from '@ant-design/icons';
import cookie from '@/utils/cookie.js';
import style from './index.less';
import CropImg from './CropImg';
import { getBase64 } from '@/utils/functions';

const processConfig = process['CONFIG'];
const { configToken } = processConfig;
const token = cookie.get(configToken);

interface FileInfo{
  uid: string;
  name: string;
  status: string;
  url: string;
  [propName: string]: any;
}
interface Props{
  fileList: FileInfo[];
  onChange: (files: FileInfo[]) => void;
  isSingle?: boolean;
  [propName: string]: any;
}


export default function UploadFile(props: Props) {
  const { fileList = [], onChange, isSingle, sizeOptions, ...uploadProps } = props;
  const [base64, setBase64] = useState();
  const [cropVisible, setCropVisible] = useState(false);

  async function transFile(file: File) {
    try {
      const base64Url = await getBase64(file);

      // @ts-ignore
      setBase64(base64Url);
      setCropVisible(true);
    } catch (error) {
      console.error(error.message);
    }
  }

  function handleSuccess(url: string) {
    const nowTime = new Date().getTime();

    setCropVisible(false);
    if (isSingle) {
      onChange([
        {
          uid: String(nowTime),
          name: String(nowTime),
          status: 'done',
          url,
        }
      ]);
    }
  }
  const hadnleRemove = (file: File) => {
    const index = fileList.findIndex((fileInfo: FileInfo) => fileInfo.uid === file.uid);
    const arr = [...fileList];

    arr?.splice(index, 1);
    onChange(arr);
  };

  const beforeUpload = (file: File) => {
    transFile(file);

    return false;
  } ;

  return (
    <div className={ style.uplaodFileW }>
      <CropImg src={ base64 || '' } onSuccess={ handleSuccess } visible={ cropVisible } changeVisible={ setCropVisible } sizeOptions={ sizeOptions } />
      <Upload beforeUpload={ beforeUpload } fileList={ fileList }
        listType="picture-card"
        onRemove={ hadnleRemove }
        onDownload={ (file: File) => {
          setBase64(file?.url);
          setCropVisible(true);
        } }
        showUploadList={{
          showPreviewIcon: true,
          showRemoveIcon: true,
          showDownloadIcon: true,
          removeIcon: <DeleteOutlined style={{ color: '#fff' }} />,
          downloadIcon: <FormOutlined title="编辑文件" style={{ color: '#fff' }} />
        }}
        { ...uploadProps }>
        <PlusOutlined />
      </Upload>
    </div>
  );
}