import React, { useState, Suspense, useCallback } from 'react';
import { Upload, message } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import cookie from '@/utils/cookie.js';
import style from './index.less';


import { uploadImg } from '@/services/publicServices';

const ImageCropModal = React.lazy(() => import('./ImageCropModal'));

const processConfig = process['CONFIG'];
const { configToken } = processConfig;
const token = cookie.get(configToken);

const sizeOptions = [
  {
    width: 690,
    height: 388,
    text: '大图',
    safeHeight: 345
  }, {
    width: 230,
    height: 152,
    text: '小图'
  }, {
    width: 720,
    height: 1280,
    text: '小视频'
  },
];

interface FileInfo{
  uid: string;
  name: string;
  status: string;
  url: string;
  [propName: string]: any;
}
interface Props{
  onSuccess: (url: string) => any;
  sizeOptions?: {width: number; height: number; text: string}[];
  [propName: string]: any;
}

const CropImg = ({ src, onSuccess, visible, changeVisible, ...props }: Props) => {
  const [loading, setLoading] = useState(false);
  const handleCropModalOk = useCallback(async ({ base64: base64Url, crop, mediaSrc, type }: any) => {
    const base64Data: string = base64Url;

    try {
      setLoading(true);
      if (base64Data?.includes('base64')) {
        const res: any = await uploadImg(base64Data);

        onSuccess(res?.data?.url);
        changeVisible(false);
      } else {
        message.error('图片转化失败');
      }
    } catch (error) {
      console.error(error);
      message.error(error.message);
    } finally {
      setLoading(false);
    }
  }, [onSuccess, changeVisible]);

  if (!visible || !src ) return null;

  return (
    <Suspense fallback={ null }>
      <ImageCropModal src={ src }
        visible={ true }
        sizeOptions={ props.sizeOptions || sizeOptions }
        onOk={ handleCropModalOk }
        loading={ loading }
        mount={ true }
        onCancel={ () => {
          changeVisible(false);
        } }
      />
    </Suspense>
  );

};

export default CropImg;
