import request from '@/utils/request';
import { UserItemInfo } from './types';
import { requestBasePath } from '@/services/login';

// 创建专家 /wind-vane-backend/user/createExpertUser
export function createExpertUser(params: UserItemInfo) {
  return request(`${ requestBasePath }/wingvate/wind-vane-backend/user/createExpertUser`, {
    method: 'POST',
    body: params
  });
}

// 更新专家信息 /wind-vane-backend/user/updateExpertUser
export function updateExpertUser(params: UserItemInfo) {
  return request(`${ requestBasePath }/wingvate/wind-vane-backend/user/updateExpertUser`, {
    method: 'POST',
    body: {
      content: params,
      userId: params.userId,
    }
  });
}