import React, { useImperativeHandle, forwardRef, useState, ForwardRefRenderFunction, MutableRefObject, useEffect } from 'react';
import { Modal, Form, Upload, Input, message, Tooltip, } from 'antd';
import systemBasePath from '@/services/login';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import './index.less';
import UploadFile from '@/components/UploadFile';

const FormItem = Form.Item;

interface FieldsValue {
  [key: string]: any;
}

interface Props {
  visible: boolean;
  onOk: (params: FieldsValue) => void;
  onCancel: () => void;
  targetExport?: any;
}

const sizeOptions = [
  {
    width: 400,
    height: 400,
    text: '头像'
  }
];

const AddExpertModal: ForwardRefRenderFunction<unknown, Props> = (
  props: Props,
  ref: MutableRefObject<any>) => {
  const { visible, onOk, onCancel, targetExport } = props;
  const [form] = Form.useForm();
  const [btnLoading, setBtnLoading] = useState(false);

  useImperativeHandle(ref, () => ({
    form
  }));

  useEffect(() => {
    form.setFieldsValue({
      nickName: targetExport?.nickName,
      avatar: (
        targetExport?.userAvatar ? [{
          uid: new Date().getTime(),
          name: new Date().getTime(),
          status: 'done',
          url: targetExport?.userAvatar,
        }] : []),
      description: targetExport?.userDescription
    });
  }, [targetExport, form]);


  const moadlOkHandle = () => {
    form.validateFields().then(async (values: FieldsValue) => {
      setBtnLoading(true);
      await onOk({
        ...values,
        userId: targetExport?.userId,
        avatar: values?.avatar?.length && values?.avatar[0]?.url
      });
      setBtnLoading(false);
    });
  };

  return (
    <Modal title={ `${ targetExport?.userId ? '编辑' : '新增' }专家` }
      visible={ visible }
      onOk={ moadlOkHandle }
      onCancel={ onCancel }
      confirmLoading={ btnLoading }
      width={ 600 }
    >
      <Form form={ form } labelCol={{ span: 4 }} wrapperCol={{ span: 18 }}>
        <FormItem label="名称" name="nickName" rules={ [{ required: true }] }>
          <Input placeholder="请输入专家名称" />
        </FormItem>
        <FormItem label="头像" name="avatar" valuePropName="fileList" rules={ [{ required: true, message: '请上传头像' }] }>
          <UploadFile sizeOptions={ sizeOptions } isSingle={ true } listType="picture-card" />
        </FormItem>
        <FormItem label="专家身份" name="description" rules={ [{ required: true }] }>
          <Input.TextArea autoSize={{ minRows: 10 }} placeholder="请输入专家身份" />
        </FormItem>
      </Form>
    </Modal>
  );
};

export default forwardRef(AddExpertModal);