import React, { ReactElement, FC, useMemo } from 'react';
import { Popover } from 'antd';

export interface Props {
  imgUrls: string[] | string;
  trigger?: string;
  placement?: 'left' | 'top' | 'right' | 'bottom' | 'topLeft' | 'topRight' | 'bottomLeft' | 'bottomRight' | 'leftTop' | 'leftBottom' | 'rightTop' | 'rightBottom' | undefined;
  children?: any;
}

const smallImgstyle = {
  width: '100px'
};

const previewImgStyle = {
  maxWidth: '600px',
  maxHeight: '600px'
};

const PreviewImg: FC<Props> = ({ imgUrls, trigger = 'hover', placement = 'rightBottom', children }: Props) => {
  const isArray = useMemo(() => {
    return Array.isArray(imgUrls);
  }, [imgUrls])
;

  return (
    isArray
      ? imgUrls?.map((item: string) => {
        <Popover content={ <img src={ item } style={ previewImgStyle } alt="" /> }
          { ... { trigger, placement } }
        >
          <img style={ smallImgstyle } src={ item } alt="" />
        </Popover>;
      })
      : (
        <Popover content={ <img src={ imgUrls } style={ previewImgStyle } alt="" /> }
          { ... { trigger, placement } }
        >
          {children ? children : <img style={ smallImgstyle } src={ imgUrls } alt="" />}
        </Popover>)
  );
};

export default PreviewImg;
