import React, { useEffect, useRef, useState, useImperativeHandle, ReactElement, ForwardRefRenderFunction, MutableRefObject, forwardRef } from 'react';
import { Button, Table } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import FormRender, { FormItem } from '../FormRender';
import styles from './index.less';
import { getRequestParams } from '@/utils/functions';
import { ListQueryRequestBody } from '@/types/types';

export type PageInfo = { current?: number; pageSize?: number }
export type OnSearch = { searchParam: {[keyname: string]: any}; pageInfo: PageInfo }

interface ColType {
  span: number;
}

interface FormItemLayout {
  labelCol: ColType;
  wrapperCol: ColType;
}

interface KeyValue {
  [key: string]: any;
}
interface SortColumns {
  field?: string;                               // key
  order?: undefined | string;                   // 排序方式
}

interface Pagination {
  current: number;
  pageSize: number;
}

export interface Props {
  formItem?: FormItem[];                        // FormRender的查询项配置
  columns?: ColumnsType;                        // Table Columns
  children?: ReactElement | ReactElement[];     // 默认QueryTable展示Table，如果传入children，则展示children
  plusAction?: ReactElement | ReactElement[];   // 出查询和重置以外的按钮
  initialValues?: KeyValue;                     // FormRender初始化数据
  formRef?: MutableRefObject<any>;              // FormRender的ref
  rowKey: string;                               // Table rowKey
  formItemLayout?: FormItemLayout;              // FormItem的labelCol和wrapperCol
  query: (params: KeyValue) => void;            // 查询函数
  list: KeyValue[];                             // Table dataSource数据
  total: number;                                // Table total总数据条数
  loading: boolean;                             // 请求loading状态
  defaultPageSize?: number;                     // 初始pagesize
  pageSizeOptions?: string[];                   // 每页多少条数据
  notQueryWhilestart?: boolean;                 // 首页进入页面是否查询
}

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 18 }
};


const QueryTable: ForwardRefRenderFunction<unknown, Props> = (
  props: Props,
  ref: MutableRefObject<any>,
) => {
  const {
    formItem = [],
    columns,
    rowKey = 'key',
    initialValues = {},
    plusAction, query,
    list = [],
    total = 0,
    loading,
    defaultPageSize,
    pageSizeOptions = ['10', '20', '50', '100'],
    notQueryWhilestart
  } = props;
  const defaultRef = useRef<any>(null);
  const formRef = props.formRef !== undefined ? props.formRef : defaultRef;
  const defaultStep = defaultPageSize ?? Number(pageSizeOptions[0]); // 如果传值了，则取defaultPageSize, 否则取每页条数配置的第一项。
  const defaultPaginetionInfo: Pagination = { current: 1, pageSize: defaultStep };
  const [paginationInfo, setpaginationInfo] = useState(defaultPaginetionInfo);
  const [sortColumns, setsortColumns] = useState<SortColumns | undefined>({}); // 排序
  // 查询
  const search = async (pageInfoParams?: PageInfo, sorter?: SortColumns) => {
    try {
      const values = await formRef.current?.getValues();
      const { current, pageSize } = paginationInfo;
      const sendSorter = sorter || sortColumns;

      const formatParams: KeyValue = getRequestParams({
        ...values,
        ...{ current, pageSize },     // 如果当前请求没传页数和每页条数，则用上次的数据
        ...pageInfoParams
      });

      setpaginationInfo({ current: formatParams.current, pageSize: formatParams.pageSize });
      
      // 判断排序参数是否传递给使用组件
      // sorter: {
      //  field: 'xxx',    // columns对应的dataIndex
      //  order: 'ascend' | 'descend' }
      if (!(sendSorter === undefined || Object.keys(sendSorter).length === 0 || sendSorter.order === undefined)) {
        formatParams.sorter = sendSorter;
      }
      const { current: paramsCurrent, pageSize: paramsPageSize, sorter: sort, ...rest } = formatParams;
      const requestParams: ListQueryRequestBody = {
        pagination: { current: paramsCurrent, pageSize: paramsPageSize },
        query: rest,
      };

      if (formatParams.sorter) {
        requestParams.sort = {
          type: formatParams.sorter.order,
          key: formatParams.sorter.field
        };
      }

      query(requestParams);
      // 参数没传sorter时sorter为undefined，但页面的排序仍然为上次请求的状态，所以需加判断，sorter存在时再去设置
      sorter && setsortColumns(sorter);
    } catch (error) {
      console.error(error);
    }
  };

  useImperativeHandle(ref, () => ({
    query: search
  }));

  useEffect(() => {
    !notQueryWhilestart && search({ ...defaultPaginetionInfo, ...initialValues });
  }, []);


  function handleReset() {
    formRef.current?.resetFrom();
    search({ current: 1 });
  }

  const paginationOnChange = (current: number, pageSize: number) => {
    // 每页条数变化
    if (pageSize !== paginationInfo.pageSize) {
      search({ current: 1, pageSize });
    } else {
      search({ current, pageSize });
    }
  };

  const tableChange = (pagination: any, filters: any, sorter: any) => {
    const { field, order } = sorter;

    if (field && (field !== (sortColumns ?? {}).field || order !== (sortColumns ?? {}).order)) {
      search({}, { field, order });
    }
  };

  return (
    <div>
      <div className={ styles.header }>
        <FormRender ref={ formRef }
          items={ formItem }
          initialValues={ initialValues }
          onPressEnter={ search }
          formItemLayout={ props.formItemLayout || formItemLayout }
        />
        <div className={ styles.actions }>
          {plusAction || <div />}
          {formItem.length > 0 && (
            <div>
              <Button onClick={ handleReset } key="reset" style={{ marginRight: '10px' }}>重置</Button>
              <Button onClick={ () => search({ current: 1 }) } type="primary" key="query">查询</Button>
            </div>
          )}
        </div>
      </div>
      <div className={ styles.body }>
        {props.children ?? <Table bordered={ true }
          size="small"
          dataSource={ list }
          columns={ columns }
          rowKey={ rowKey }
          pagination={{
            total,
            showTotal: (totalCount: number) => `共${ totalCount }条数据`,
            current: paginationInfo.current,
            pageSize: paginationInfo.pageSize,
            onChange: paginationOnChange,
            showSizeChanger: true,
            pageSizeOptions
          }}
          loading={ loading }
          onChange={ tableChange }
        />}
      </div>
    </div>
  );
  
};

export default forwardRef(QueryTable);
